          <blockquote  class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Aquaman是DC Comics出版的美国漫画书中出现的虚构超级英雄。由Paul Norris和Mort Weisinger创作，角色在More Fun Comics＃73（1941年11月）首次亮相。最初是DC的选集中的备份功能，Aquaman后来出演了几本独奏漫画系列。在20世纪50年代末和60年代的超级英雄复兴时期，被称为白银时代，他是正义联盟的创始成员。在20世纪90年代的现代时代作家更加认真地解读了Aquaman的角色，故事情节描绘了他作为亚特兰蒂斯国王的角色的重要性。</blockquote>
          <blockquote style="margin-top:-20px" class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;角色的原始1960年代动画外观留下了持久的印象，使得Aquaman在流行文化中得到广泛认可，并成为世界上最公认的超级英雄之一。关于他在超级朋友中的健康，弱势描写以及感知到的微弱力量和能力的笑话已经成为喜剧节目和站立惯例的主要内容，多次领导DC尝试使角色变得更加锐利或者在漫画书中更强大。现代漫画描写试图调和他的公众认知的各个方面，将Aquaman视为严肃和沉思，背负着不良声誉，并努力寻找一个真正的角色和目的超越他的公众方面作为被废king的国王和堕落的英雄。</blockquote>
        
